<template>
  <div class="nav_wrapper" @mousewheel.prevent @touchmove.prevent>

    <div :style="{ background: isBg }" class="header padding_15 flex_box_between">
      <div class="">
        <router-link to="/">
          <img width="76" src="@/assets/imgs/home_icon_logo.png" alt="">
        </router-link>
      </div>
      <div class="" @click="isShow=!isShow">
        <img width="15" src="@/assets/imgs/show_nav.png" alt="">
      </div>
    </div>
    
    <transition name="slide">
      <div class="nav" v-show="isShow">    
        <ul>
          <li class="nav_li" v-for="(item,index) in configNav" :key="index" :class="{'li_has_sub':item.subItems}">
            <router-link v-if="item.link&&!item.isA" class="nav_li_a" @click.native="isShow=!isShow" :to="item.link">{{item.name}}</router-link>
            <a v-else-if="item.isA" class="nav_li_a" :href="item.link">{{item.name}}</a>
            <a v-else class="nav_li_a" :class="index===isShowIndex ? 'nav_li_a_no':'nav_li_a_hass'" href="javascript:;" @click="showToggle($event,index)">{{item.name}}</a>
            <div v-if="item.subItems" class="sub_nav" :style="index===isShowIndex ? sub_nav_active_style : ''">
              <ul>
                <li v-for="(nav,index) in item.subItems" :key="index">
                  <router-link :to="nav.link" @click.native="isShow=!isShow">
                    <!-- :class="{'active':nav.link == linkClick}" -->
                    <!-- @click="treeNavSwitch(nav)" -->
                    {{nav.name}}
                  </router-link>
                </li>
              </ul>
            </div>
          </li>

        </ul>
        <div @click="isShow=!isShow" class="close_nav_box">
          <div class="close_nav"></div>
        </div>
      </div>           
    </transition>

    <div class="shadow" v-show="isShow" @click="isShow=!isShow"></div>    
    
  </div>
</template>

<script>
export default {
  data(){
    return {
      isShow:false,
      isBg:'none',
      sub_nav_active_style:{},

      isShowIndex:null,
      configNav:[
        { 
          name:'首页',
          link:'/'
        },
        { 
          name:'外包',
          subItems:[
            { link:'out-person',name: '专业人才外包'},
            { link:'out-hr',name: '人力资源外包' },
            { link:'out-pay',name: '在线工资代发' }
          ]
        },
        { 
          name:'招聘',
          subItems:[
            { link:'recruit-hunt',name: '高端猎头服务'},
            { link:'recruit-out',name: '招聘外包服务' },
          ]
        },
        { 
          name:'响单单',
          isA:true,
          link:'https://www.xiangdandan.cn/'
        },
        { 
          name:'关于我们',
          link:'/about'
        },
      ]        
    }
  },
  mounted () {
    window.addEventListener('scroll', this.windowScroll)
  },  
  methods:{
    windowScroll () {
      //滚动条距离页面顶部的距离
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop //原生兼容
      // console.log(scrollTop) 
      if(scrollTop>200){
        this.isBg = 'rgba(31, 33, 42, 0.75)'
      }else{
        this.isBg = 'none'
      }
    },   
    hideSilde(){
      this.isShow=!this.isShow
    },
    showToggle:function(e,index){
      // console.log(index);
      // console.log(e.target.nextElementSibling.firstElementChild.getElementsByTagName('li').length);
      const liNum = e.target.nextElementSibling.firstElementChild.getElementsByTagName('li').length
      this.sub_nav_active_style = {
        height:`${liNum*50}px`
      }

      if(index != this.isShowIndex){
        this.isShowIndex = index;  
      }else{
        this.isShowIndex = null
      }
    },
  }
}
</script>

<style scoped lang="scss">
.header{
  height: 50px;
  // background: rgba(31, 33, 42, 0.75);
  border-bottom:1px solid #4d5356;
  position: fixed;
  width: 100%;
  transition:all .5s ease 0s;

}
.shadow{
  position: fixed;
  width:100%;
  height: 100%;
  background: rgba(0,0,0,.3);
  top:0;
  right:0;
  z-index: 9999;
}
.nav{
  font-size: 15px;
  position: fixed;
  top:0;
  right:0;
  width:175px;
  background: #1e1e1e;
  height: 100%;
  z-index: 99999;
  /* @include flex-center; */
  flex-direction:column;
  .nav_li{
    min-height: 49px;
    line-height: 49px;
    overflow: hidden;
    .nav_li_a{
      padding: 0 15px 0 20px;
      display: inline-block;
      width: 100%;
      height: 100%;
      color: #fff;
      border-bottom:1px solid #4d5356;
      // margin: 0 -15px;
    }
    .nav_li_a_hass,.nav_li_a_no{
      position: relative; 
    }
    .nav_li_a_hass:after,.nav_li_a_no:after{
      content: '';
      position: absolute; 
      display: inline-block;
      width: 13px;
      height: 7px;
      right: 15px;
      top:50%;
      margin-top: -3px;
      background: url(../assets/imgs/nav_down.png) no-repeat;
      background-size:cover; 
      transform: rotate(0deg);
      transition:all .5s ease 0s;
    }
    .nav_li_a_no:after{
      transform: rotate(180deg);
    }
    .sub_nav{
      // display: none;
      height: 0;
      background: #101010;
      transition:all .5s ease 0s;
      a{
        display: inline-block;
        color: #999;
        font-size: 13px;
        padding: 0 15px 0 30px;
        height: 100%;
        width: 100%;
      }
    }
    .sub_nav_active{
      // display: block;
      // height: auto;
      height: 100px;
    }
  }
  .have_sub{
    position: relative;
  }
  .close_nav_box{
    position: absolute;
    top: 0px;    
    right: 0;
    height: 50px;
    width: 40px;
    .close_nav{
      cursor: pointer;
      height: 14px;
      width: 14px;
      background: url(../assets/imgs/close_icon.png);
      background-size: 100% 100%; 
      position: absolute;
      right: 15px;
      top: 18px;
    }
  }
}


.slide-enter-active{
  transition:all 0.4s ease-out;
}
.slide-enter, .slide-leave-active{
  transform:translateX(200%);
  transition:all 0.4s ease-out;
}

</style>