import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
  // 跳转后返回顶部
  window.scrollTo(0,0);
  next()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')